.Landing {
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  /*justify-content: center;*/
  align-items: center;
  color: #D8DEE9;
  padding-top: 10rem;
}

.Introduction {
  /*flex: 0 1 auto;*/
  /*align-self: center;*/
  font-weight: 700;
  font-size: 3.1rem;
  padding-bottom: 2.2rem;
}

.Greet {
  font-family: 'Ubuntu', sans-serif;
}

.Tag {
  font-family: 'Source Code Pro', monospace;
}

.Name {
  color: #c8a026;
}

.Punctuation {
  color: #2d9f51;
}

.Tagline {
  /*flex: 0 1 auto;*/
  /*align-self: center;*/
  font-size: 1.6rem;
  font-weight: 400;
  padding-bottom: 1.5rem;
}

@media (max-width: 767px) {
  .Introduction {
    font-size: 2rem;
    padding-bottom: 1.5rem;
  }

  .Tagline {
    font-size: 1.2rem;
    padding-bottom: 1rem;
  }
}

@media (max-width: 480px) {
  .Introduction {
    font-size: 1.5rem;
    padding-bottom: 1rem;
  }

  .Tagline {
    font-size: 1rem;
    padding-bottom: 0.8rem;
  }
}
