.About {
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  /*justify-content: center;*/
  align-items: center;
  padding-top: 10rem;
  color: #D8DEE9;
}

.AboutContent {
  width: 50%;
  flex: 0 1 auto;
  font-size: 1rem;
  line-height: 1.5rem;
}

.AboutContent p {
  font-weight: 300;
  padding: 0.5rem 0;
}

.Highlight {
  color: #C8A026;
}

@media (max-width: 499px) {
  .AboutContent {
    font-size: 0.85rem;
  }
}

