.Projects {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  padding-top: 10rem;
}

.Projects .Item {
  background-size: cover;
  background-position: center center;
  width: 300px;
  height: 300px;
}

.Projects .Item:hover {
  filter: brightness(80%);
}

.ItemInfoContainer {
  position: relative;
  max-width: 300px;
  min-height: 300px;
  margin: auto;
  overflow: hidden;
}

.ItemInfoContainer:hover {
  background: rgba(0, 0, 0, 0.6);
}

.ItemTitle {
  margin: 0;
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  top: -200px;
  transform: translateY(-50%);
  text-align: center;
  transition: 0.5s;
  color: #D8DEE9;
}

.ItemInfoContainer:hover .ItemTitle {
  top: 10px;
  transform: translateY(0);
}

.ItemDescription {
  color: #D8DEE9;
  position: absolute;
  top: 400px;
  opacity: 0.8;
  transition: 0.8s;
  padding: 10px;
  font-size: 1rem;
  line-height: 1.2rem;
}

.ItemDescription p {
  font-weight: 300;
  padding: 0.5rem 0;
}

.ItemInfoContainer:hover .ItemDescription {
  top: 50%;
  transform: translateY(-50%);
}

.ItemDescription a:any-link {
  color: #D8DEE9;
  display: inline-block;
}
