.NavigationItem {
  display: flex;
  align-self: flex-start;
}

.NavigationItem a {
  color: #D8DEE9;
  text-decoration: none;
  width: 100%;
  display: block;
  position: relative;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.NavigationItem a:before, a:after {
  position: absolute;
  opacity: 0;
  font-size: 1.7rem;
  top: 0;
  transition: all 0.3s;
}

.NavigationItem a:before {
  content: '(';
  left: 0;
  transform: translateX(10px);
}

.NavigationItem a:after {
  content: ')';
  right: 0;
  transform: translateX(-10px);
}

.NavigationItem a:hover:before,
.NavigationItem a:hover:after {
  opacity: 1;
  transform: translateX(0px);
}

.NavigationItem a.active {
  color: #c8a026;
}
