.Logo {
  width: 50px;
  height: 50px;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 1.5rem;
  margin-left: 3rem;
  overflow: visible;
  border: 5px solid #c8a026;
  position: relative;
  transform: rotate(0deg);
  transition: transform .4s ease-in-out;

}

.LogoText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(0deg);
  transition: transform .4s ease-in-out;
  color: #D8DEE9;
  font-size: 200%;
  font-weight: 900;
}
